.convert-form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	/*background-color: var(--theme-white);*/
	background-color: #fff;
	text-align: center;
	/*max-width: 1380px;*/
	width: 80%;
	min-height: calc(100vh - 8.25em);
	margin: 0 auto;
	/*border-radius: 0.25em;*/
	-webkit-box-shadow: 0 2rem 6rem rgba(0, 0, 0, .3);
	        box-shadow: 0 2rem 6rem rgba(0, 0, 0, .3);
}

.convert-submit-button {
	font-weight: 400;
	padding: 10px 15px;
	text-transform: uppercase;
	text-align: center;
	display: block;
	/*	color: #fff;
	background-color: var(--theme-blue);*/
	color: var(--theme-blue);
	background-color: #fff;
	cursor: pointer;
	border: 1px solid var(--theme-blue);
	max-width: 400px;
	width: 100%;
	margin: 60px auto;
	/*	position: relative;
	left: 60%;*/
}

.platform-list-container {
	display: -ms-flexbox;
	display: flex;
	width: 80%;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	margin: 0 auto;
}

.platform-list-container label {
	color: var(--theme-grey);
	font-size: 2em;
}

.platform-list-container-left {
	-ms-flex: 1 1;
	    flex: 1 1;
	margin: 0 80px;
	text-align: left;
}
.platform-list-container-right {
	-ms-flex: 1 1;
	    flex: 1 1;
	margin: 0 80px;
	text-align: left;
}

.convert-header {
	color: var(--theme-dark-blue);
	font-size: 2.7em;
	font-weight: 300;
	text-transform: uppercase;
	max-width: 650px;
	margin: 0 auto;
	margin-top: 5vh;
}

.step-one {
	margin: 30px;
	color: var(--theme-grey);
}

@media only screen and (max-width: 400px) {
	.convert-form {
		margin: 0;
		width: 100%;
	}
}

@media only screen and (max-width: 600px) {
	.convert-header {
		font-size: 2rem;
	}
}

@media only screen and (max-width: 800px) {
	.platform-list-container {
		-ms-flex-direction: column;
		    flex-direction: column;
	}
}

@media only screen and (max-width: 1100px) {
	.platform-list-container-left {
		margin: 0 20px;
	}	

	.platform-list-container-right {
		margin: 0 20px;
	}
}

@media only screen and (max-width: 1300px) { 
	.platform-list-container {
		width: 100%;
	}

};



