* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
	font-family: 'Open Sans', sans-serif;
	font-weight: 400;
	/*line-height: 1.4;*/

	margin: 0;
	padding: 0;
	/*font-family: sans-serif;*/
	/*background-color: var(--theme-white);*/
	background: -webkit-gradient(linear, left top, right bottom, from(var(--theme-light-blue)), to(var(--theme-blue)));
	background: -webkit-linear-gradient(left top, var(--theme-light-blue), var(--theme-blue));
	background: -o-linear-gradient(left top, var(--theme-light-blue), var(--theme-blue));
	background: linear-gradient(to right bottom, var(--theme-light-blue), var(--theme-blue));
	background-size: cover;
	background-repeat: no-repeat;
	min-height: 100vh;
}

body,
html {
	/*width: 100%;*/
	/*height: 100%;*/
}




